<template>
  <v-toolbar height="100%">
        <v-toolbar-title class="hidden-sm-and-down">FlatRent</v-toolbar-title>
    <v-spacer/>
        <v-btn class="mx-2 hidden-sm-and-down" to="/" color="success">Find Flat</v-btn>
        <v-btn  class="mx-2 hidden-sm-and-down" to="/" color="success">Find Room</v-btn>
        <v-btn   class="mx-2 hidden-sm-and-down" to="/roommates" color="success">Find Roommate</v-btn>

        <v-btn class="mx-2 hidden-sm-and-down"  to="/addnew" color="error">Add New</v-btn>

        <template v-if="!isSignedIn">
            <v-btn class="mx-2" to="/signin" color="info">Sign In</v-btn>
            <v-btn class="mx-2" to="/signup" color="info">Sign Up</v-btn>
        </template>
        <template v-else>
            <router-link to="/profile">
                <v-avatar>
                    <img src="https://blog.archonia.com/content/images/2016/05/batmanIcon.jpg"/>
                </v-avatar>
            </router-link>
            <v-btn class="mx-2" to="/" color="info" @click="signOut">Sign Out</v-btn>
        </template>
  </v-toolbar>
</template>

<script>
import { mapState } from "vuex";
import store from "@/store";
import { SIGN_OUT } from "@/vuex/actions";

export default {
  computed: mapState(["isSignedIn"]),
  methods: {
    signOut() {
      store.dispatch(SIGN_OUT);
    }
  }
};
</script>

<style lang="sass" scoped>
    .v-toolbar
        height: 8vh
</style>
